$easeInBack:  cubic-bezier(0.600, -0.280, 0.735, 0.045);
$easeOutBack:  cubic-bezier(0.175,	0.885, 0.320, 1.275);
$easeInOutBack:  cubic-bezier(0.680, -0.550, 0.265, 1.550);

html,body,h1,h2,h3,h4,h5,h6,p{
	margin:0;
	padding:0;
}
:root{
	
	--pad:2vmin;
}
html{
	font-family: 'Oxanium', sans-serif;

	// dark
	transition: background-color 450ms linear;
	background-color: hsl(0, 10%, 0%);
	background-image:linear-gradient(hsla(0, 10%, 38%, 0.5), hsla(0, 10%, 28%, 0.5)) ;
	
	// light
	&.loading{
		background-color: hsl(0, 10%, 100%);
		background-image:linear-gradient(hsla(0, 10%, 88%, 0.5), hsla(0, 10%, 68%, 0.5)) ;
	}
}

html, body{
	height: 100%;
}

@import url('https://fonts.googleapis.com/css2?family=Oxanium:wght@700&display=swap');


header{
	position: absolute;
	z-index: -1;
	top:0;
	right:0;
	left:0;
	//overflow: hidden;
	// above feedback
	bottom:6rem;
	display: grid;
	text-align: center;
	justify-content: center;

	> * {
		max-height:100vh;
		grid-area: 1/1/1/1;
		visibility: hidden;
	}
	h1{
		background-color: black;
		color: hsl(0, 10%, 100%);
		display: inline-block;
		padding:var(--pad) calc(var(--pad) * 2);
		text-transform: uppercase;
		align-self: self-start;
		justify-self: center;
		margin-top:var(--pad);
	}
	h1{
		text-shadow: rgba(0,0,0,0.56) 0 1px 4px,
					rgba(0,0,0,0.56) 0 2px 4px,
					rgba(0,0,0,0.56)0 3px 4px,
					rgba(255,255,255,0.96) 0 0 4px;
	}
	h2,p{
		text-shadow: rgba(255,255,255,0.96) 0 1px 4px,
					rgba(255,255,255,0.7) 0 2px 4px,
					rgba(255,255,255,0.5) 0 3px 4px,
					rgba(0,0,0,0.56) 0 0 4px;
		
	}
	h2{
		margin-top:auto;
	}
	.logo{
		align-self: center;
		justify-self: center;
	}
}
.logo, h1, h2{
	// position: absolute;
	// top:0;
	// left:0;
}

.logo{
	display: flex;
	flex-direction: column;
	align-items: center;
	// max-height:10vh;
	width:auto;
	color:transparent;
	visibility: visible;
	overflow: hidden;

	img {
		margin: auto;
		display: block;
		height:auto;
		width:auto;
		max-width:100vmin;
		max-height:100vmin;
		// height:100%;
	}
}

main{


	--person-a-x:0;
	--person-a-y:0;
	--person-b-x:0;
	--person-b-y:0;

	display: grid;
	position: relative;

	height: 100%;
	min-height: 320px;
	max-height: 100vh;

	object-fit: contain;

	// show a graphic but hide contents
	&::before, &::after{
		transition: 400ms ease-out opacity;
		opacity:0;
	}
}

// put them all in the same place in the grid
main > * {
	grid-area: 1/1/1/1;
	transition: 400ms ease-out opacity;

	max-width:100%;
	max-height:100vh;

	height:auto;
	object-fit: contain;
	margin:auto;
}

.loading {

	header{
		
		> *{
			visibility:visible;
			animation:Fade 400ms ease-in 1 0s both;
		}	
		.logo{
			
		}
	}
	video{
		opacity:0.5;
	}
	#face{
		display: none;
	}
	main{

		--loader-size:8vmin;
		--loader-duration:4;
		overflow:hidden;

		pointer-events: none;
		
		// show a graphic but hide contents
		&::before,&::after{
			content: '';
			opacity: 1;
			
			// height:6vh;
			// width:6vh;
			width:100%;
			height:100%;
			background-image:linear-gradient(blue,blue);
			background-size: var(--loader-size) var(--loader-size);
			background-position: top left;
			background-repeat: no-repeat;

			position: absolute;
			top:0;
			left:0;
			
			display: block;
			animation:Loading calc(var(--loader-duration) * 1s) 0s infinite $easeInOutBack;
		}

		&::after{
			animation-delay:calc( var(--loader-duration) / 4 * 0.02s );
			filter: blur(5px);
		}

		> *:not(p){
			visibility: hidden;
			opacity:0;
			display: none;
		}

		#feedback{
			text-align: center;
		}
	}

}


main{
	#button-midi{
		display: none;
	}
	video{
		transform: scaleX(-1);
	}
	video,canvas{
		border-radius:12px;
		overflow: hidden;
		// scale to fit screen!
	}
}

main.midi{
	#button-midi{
		display: block;
	}
}

main.video{
	img{
		display: none;
	}
}
main.img{
	video{
		display: none;
	}
}

main{
	button{
		display: none;
	}
}

main.active{
	button{
		display: block;
	}
}

p{
	pointer-events: none;
	font-weight:bold;
	text-align:right ;
	color:white;
	display: inline-block;
	padding:var(--pad);
	text-shadow : rgba(255,255,255,0.96) 0 1px 4px,
					rgba(0,0,0,0.96) 0 0 4px;

}

#feedback{
	box-sizing: border-box;
	position: fixed;
	z-index: 303;
	bottom:var(--pad);
	// left:var(--pad);
	right:var(--pad);
	max-width: calc(100% - 2 * var(--pad));
	
	// cool carat
	&::after{
		content:'';
		width: 1rem;
		height:100%;
		background-color: yellow;
		display: inline-block;
	}
}

#feedback:hover ~ canvas{
	opacity:0.5;
}

// we need this to visually look the same size as the video
#controls{
	
	display: block;
	margin:auto;
	padding:0;
	
	// width:100%;
	pointer-events: none;

	max-width:var(--width);
	max-height:var(--height);

	> *{
		pointer-events: auto;
	}

	// we need media queries to complete
	
	// landscape
	@media (orientation: landscape) {
		height:100vh;
		width:calc(var(--width) / var(--height) * 100vh );
	}
	@media (orientation: portrait) {
		// portrait
		width:100vw;
		height:calc(var(--height) / var(--width) * 100vw );
	}

	overflow: hidden;
	position: relative;

	// &::before{
	// 	display: block;
	// 	content:'';
	// 	// width:calc(var(--width) / var(--height) * 100%);
	// 	// height:calc( 100% );
	// 	width:100%;
	// 	padding-bottom:calc(var(--width) / var(--height) * 100%);
	// }
}

fieldset{
	border:0;
	margin:var(--pad);
	padding:0;
}

button{
	
	appearance: none;
	border:0;
	background-color: transparent;
}

// button that covers people
button.person{
	cursor:pointer;
	position: absolute;	
	z-index:202;
	
	border-radius: 100%;
	//border:2px solid white;

	color:transparent;
	opacity: 0;
	transition: 400ms ease-in opacity, left 9ms linear, top 9ms linear;

	&:hover, &:focus{
		
		outline: none;
	}

	&:hover{
		opacity: 0.8;
	}

	svg{
		width: 100%;
		//height:auto;
		fill:orange;
		stroke-width: 3px;
		stroke:purple;
		//transform: scaleX(-1);
		pointer-events: none;
		circle{
			stroke:black;
			fill:white;
		}
	}
}
.active button.person{
	display: block;

	
}


button#person-a{
	
	// left:calc( 1px * var(--person-a-x,0) );
	right:calc( 100% * var(--person-a-x,0) / var(--width,640) );
	top:calc( 100% * var(--person-a-y,0) / var(--height,640) );
	
	width:calc( 100% * var(--person-a-w,0) / var(--width,640) );
	height:calc( 100% * var(--person-a-h,0) / var(--height,640) );
}

// move into position
button#person-b{
	left:calc( 1px * var(--person-b-x,0) );
	top:calc( 1px *var(--person-b-y,0) );
	width:calc( 1px * var(--person-b-w,0) );
	height:calc( 1px * var(--person-b-h,0) );
}

button#button-video{
	position: absolute;
	top:0;
	left:0;
	width: 100%;
	// right:0;
	bottom:0;
	z-index:-1;
}

#button-midi{
	background-image:url(assets/logos/midi.svg);
	background-repeat:no-repeat;
}
@keyframes Fade{
	from{
		opacity: 0;
	}
	to{
		opacity: 1;
	}
}
@keyframes Loading{
	0%{
		transform:translateX( 0 );
		//background-color: hsl(90, 60%, 40%);
		background-image:linear-gradient(90deg, hsl(90, 60%, 40%), hsl(90, 60%, 40%));
	}
	10%{
		background-image:linear-gradient(90deg, hsl(180, 60%, 40%),hsl(180, 60%, 40%));
	}
	20%{
		transform:translateX( calc(100% - var(--loader-size)) );
		//background-color: hsl(180, 60%, 40%);
		background-image:linear-gradient(90deg, hsl(180, 60%, 40%),hsl(180, 60%, 40%));
	}
	30%{
		background-image:linear-gradient(90deg, hsl(270, 60%, 40%),hsl(270, 60%, 40%));
	}
	40%{
		transform:translateX( calc(100% - var(--loader-size)) ) translateY( calc( 100% - var(--loader-size) ) );
		//background-color: hsl(270, 60%, 40%);
		background-image:linear-gradient(90deg, hsl(270, 60%, 40%),hsl(270, 60%, 40%));
	}
	50%{
		background-image:linear-gradient(90deg, hsl(360, 60%, 40%), hsl(360, 60%, 40%));
	}
	70%{
		transform:translateX( 0 )  translateY( calc( 100% - var(--loader-size) ) );
		//background-color: hsl(360, 60%, 40%);
		background-image:linear-gradient(90deg, hsl(360, 60%, 40%), hsl(360, 60%, 40%));
	}
	80%{
		background-image:linear-gradient(90deg, hsl(90, 60%, 40%),hsl(90, 60%, 40%));
	}
	100%{
		transform:translateX( 0 );
		//background-color: hsl(90, 60%, 40%);
		background-image:linear-gradient(90deg, hsl(90, 60%, 40%),hsl(90, 60%, 40%));
	}
}